'use client'

import React, { useState, useEffect } from 'react'
import { Check } from 'lucide-react'

interface SkillNode {
    id: string
    label: string
    x: number
    y: number
    category: 'AI' | 'Web' | 'Hardware' | 'Theory'
    connections: string[]
}

const SKILL_NODES: SkillNode[] = [
    // AI
    { id: 'python', label: 'Python', x: 20, y: 30, category: 'AI', connections: ['pytorch', 'nlp', 'cv'] },
    { id: 'pytorch', label: 'PyTorch', x: 25, y: 50, category: 'AI', connections: ['cv', 'nlp'] },
    { id: 'cv', label: 'ËÆ°ÁÆóÊú∫ËßÜËßâ', x: 15, y: 70, category: 'AI', connections: [] },
    { id: 'nlp', label: 'Ëá™ÁÑ∂ËØ≠Ë®ÄÂ§ÑÁêÜ', x: 35, y: 75, category: 'AI', connections: [] },

    // Web
    { id: 'ts', label: 'TypeScript', x: 70, y: 20, category: 'Web', connections: ['react', 'node'] },
    { id: 'react', label: 'React', x: 85, y: 35, category: 'Web', connections: [] },
    { id: 'node', label: 'Node.js', x: 75, y: 50, category: 'Web', connections: [] },

    // Hardware
    { id: 'cpp', label: 'C++', x: 50, y: 50, category: 'Hardware', connections: ['embedded', 'fpga'] },
    { id: 'embedded', label: 'ÂµåÂÖ•ÂºèÂºÄÂèë', x: 45, y: 80, category: 'Hardware', connections: [] },
    { id: 'fpga', label: 'FPGA', x: 60, y: 70, category: 'Hardware', connections: [] },

    // Theory
    { id: 'algo', label: 'ÁÆóÊ≥ïÂü∫Á°Ä', x: 50, y: 15, category: 'Theory', connections: ['python', 'cpp', 'ts'] },
]

interface Props {
    selectedSkills: string[]
    onToggle: (id: string) => void
}

export default function MeshSkillSelector({ selectedSkills, onToggle }: Props) {
    return (
        <div className="mesh-container glass" style={{
            width: '100%',
            height: '500px',
            position: 'relative',
            overflow: 'hidden',
            borderRadius: '24px',
            background: 'radial-gradient(circle at 50% 50%, rgba(99, 102, 241, 0.05), transparent)'
        }}>
            <svg style={{ position: 'absolute', top: 0, left: 0, width: '100%', height: '100%', pointerEvents: 'none' }}>
                {SKILL_NODES.map(node => (
                    node.connections.map(connId => {
                        const target = SKILL_NODES.find(n => n.id === connId)
                        if (!target) return null
                        const isActive = selectedSkills.includes(node.id) && selectedSkills.includes(target.id)
                        return (
                            <line
                                key={`${node.id}-${connId}`}
                                x1={`${node.x}%`} y1={`${node.y}%`}
                                x2={`${target.x}%`} y2={`${target.y}%`}
                                stroke={isActive ? 'var(--primary)' : 'rgba(255,255,255,0.1)'}
                                strokeWidth={isActive ? 2 : 1}
                                strokeDasharray={isActive ? "none" : "4 2"}
                                style={{ transition: 'all 0.5s ease' }}
                            />
                        )
                    })
                ))}
            </svg>

            {SKILL_NODES.map(node => {
                const isSelected = selectedSkills.includes(node.id)
                return (
                    <div
                        key={node.id}
                        onClick={() => onToggle(node.id)}
                        style={{
                            position: 'absolute',
                            left: `${node.x}%`,
                            top: `${node.y}%`,
                            transform: 'translate(-50%, -50%)',
                            padding: '0.6rem 1.2rem',
                            borderRadius: '100px',
                            cursor: 'pointer',
                            zIndex: 2,
                            display: 'flex',
                            alignItems: 'center',
                            gap: '0.5rem',
                            fontSize: '0.85rem',
                            fontWeight: 600,
                            transition: 'all 0.3s cubic-bezier(0.175, 0.885, 0.32, 1.275)',
                            background: isSelected ? 'var(--primary)' : 'rgba(255,255,255,0.03)',
                            border: isSelected ? '1px solid var(--primary)' : '1px solid var(--glass-border)',
                            color: isSelected ? 'white' : '#94a3b8',
                            boxShadow: isSelected ? '0 0 20px rgba(99, 102, 241, 0.4)' : 'none'
                        }}
                        className="mesh-node"
                    >
                        {isSelected && <Check size={14} />}
                        {node.label}
                    </div>
                )
            })}

            <div style={{ position: 'absolute', bottom: '20px', left: '20px', fontSize: '0.75rem', color: '#64748b' }}>
                üí° ÊèêÁ§∫ÔºöÁÇπÂáªËäÇÁÇπÁÇπ‰∫ÆÊÇ®ÁöÑÊäÄËÉΩÊ†ëÔºåÁ∫øÊÆµËøûÊé•‰ª£Ë°®Áõ∏ÂÖ≥ÊäÄÊúØÊ†à„ÄÇ
            </div>
        </div>
    )
}
